import LocalPhoneIcon from '@material-ui/icons/LocalPhone';
import EmailIcon from '@material-ui/icons/Email';
import TelegramIcon from '@material-ui/icons/Telegram';

import React from 'react';
import styles from './ContactUs.module.scss';
import { Popover } from '@mui/material';

export default () => {
	const [anchorEl, setAnchorEl] = React.useState(null);

	const handleClick = (event) => {
		setAnchorEl(event.currentTarget);
	};
	const handleClose = () => {
		setAnchorEl(null);
	};

	const open = Boolean(anchorEl);
	const id = open ? 'simple-popover' : undefined;

	return (
		<div className={styles.contactUs}>
			<div className={styles.btnRight}>RU</div>
			<div className={styles.btnCenter} aria-describedby={id} onClick={handleClick}>
				<div className={styles.btnText}>HELP AND SUPPORT</div>
			</div>
			<div className={styles.btnLeft}>ООО СЕРВИС СОФТ 2004 - 2023</div>

			<Popover
				id={id}
				open={open}
				anchorEl={anchorEl}
				onClose={handleClose}
				elevation={0}
				sx={{ boxShadow: 'none', background: 'none' }}
				anchorOrigin={{
					vertical: 'top',
					horizontal: 'center',
				}}
				transformOrigin={{
					vertical: 'bottom',
					horizontal: 'center',
				}}
			>
				<div className={styles.iconContainer}>
					<a href="tel:88002500104">
						<LocalPhoneIcon
							className={styles.icon}
							style={{
								color: '#F07C00',
								backgroundColor: 'white',
							}}
						/>
					</a>

					<a href="mailto:lookout@ssoft24.com" target="_blanc">
						<EmailIcon
							className={styles.icon}
							style={{
								color: 'white',
								backgroundColor: '#F07C00',
							}}
						/>
					</a>

					<a href="#" target="_blanc">
						<TelegramIcon
							className={styles.icon}
							style={{
								color: 'white',
								backgroundColor: '#229ED9',
							}}
						/>
					</a>
				</div>
			</Popover>
		</div>
	);
};
